import React, { memo } from 'react';
import { Button, Form, Input, InputNumber, Select } from 'antd';

import { convertOptionSelect } from '../utils';

const { Option } = Select;

function ProductForm(props) {
  const {
    isHiddenSubmit,

    formName,

    form,
    optionStyle,
    suppliers,
    categories,

    onFinish,
  } = props;

  return (
    <Form
      form={form}
      className=""
      name={formName}
      labelCol={{ span: 8 }}
      wrapperCol={{ span: 16 }}
      style={optionStyle}
      onFinish={onFinish}
      initialValues={{
        gender: 'female',
      }}
    >
  

      <Form.Item
        label="Nh cung cp"
        name="supplierId"
        rules={[
          {
            required: true,
            message: 'Vui lng chn nh cung cp',
          },
        ]}
      >
        <Select
          // options={convertOptionSelect(suppliers)}
        >
          {suppliers.map((item) => (
            <Option value={item.id || item._id}>{item.name}</Option>
          ))}
        </Select>
      </Form.Item>

      <Form.Item
        label="Danh mc sn phm"
        name="categoryId"
        rules={[
          {
            required: true,
            message: 'Vui lng chn danh mc',
          },
        ]}
      >
        <Select options={convertOptionSelect(categories)} />
      </Form.Item>

      <Form.Item
        label="Tn sn phm"
        name="name"
        rules={[
          { required: true, message: 'Vui lng nhp tn sn phm' },
          { max: 50, message: 'Ti a 50 k t' },
        ]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="Gi gc"
        name="price"
        rules={[
          {
            type: 'number',
            min: 0,
            message: 'Vui lng nhp gi gc t 0 n 100',
          },
          { required: true, message: 'Vui lng nhp gi gc' },
        ]}
      >
        <InputNumber style={{ width: '100%' }} />
      </Form.Item>

      <Form.Item
        label="Chit khu (%)"
        name="discount"
        rules={[
          {
            type: 'number',
            min: 0,
            max: 100,
            message: 'Vui lng nhp gim gi t 0 n 100',
          },
          { required: true, message: 'Vui lng nhp gim gi' },
        ]}
      >
        <InputNumber style={{ width: '100%' }} />
      </Form.Item>

      <Form.Item
        label="Tn kho"
        name="stock"
        rules={[
          {
            type: 'number',
            min: 0,
            message: 'Vui lng nhp tn kho ln hn 0',
          },
          { required: true, message: 'Vui lng nhp tn kho' },
        ]}
      >
        <InputNumber style={{ width: '100%' }} />
      </Form.Item>

      <Form.Item label="M t" name="description">
        <Input />
      </Form.Item>

      {!isHiddenSubmit && (
        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Form.Item>
      )}
    </Form>
  );
}

export default memo(ProductForm);
